.loader {
  width: clamp(6.25rem, 7.792vw + 4.399rem, 13.75rem);
  aspect-ratio: 1;
  padding: 10px;
  box-sizing: border-box;
  display: grid;
  background: #fff;
  filter: blur(5px) contrast(10) hue-rotate(300deg);
  mix-blend-mode: darken;
}
.loader:before,
.loader:after{
  content: "";
  grid-area: 1/1;
  width: clamp(2.5rem, 3.896vw + 1.575rem, 6.25rem);
  aspect-ratio: 1;
  background: #587b46;
  animation: l7 2s infinite;
}
.loader:after{
  animation-delay: -1s;
}
@keyframes l7{
  0%   {transform: translate(   0,0)}
  25%  {transform: translate(100%,0)}
  50%  {transform: translate(100%,100%)}
  75%  {transform: translate(   0,100%)}
  100% {transform: translate(   0,0)}
}