.main{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  &__name__page{
    font-family: 'PoppinsBold', serif;
    font-size: clamp(2.625rem, 1.299vw + 1.692rem, 3.25rem);
    text-align: end;
    margin: {
      bottom: clamp(0.313rem, 0.325vw + 0.235rem, 0.625rem);
    };
    width: 85%;
  }

    @media only screen and (max-width: 950px) {
      &{
        height: 100%;
        .main__name__page{
          display: none;
        }
      }
    }
}